import React, { useEffect } from 'react';
import { BsFillVolumeMuteFill, BsFillVolumeUpFill } from 'react-icons/bs';
import useLocalStorage from 'use-local-storage';
import useSound from 'use-sound';
import useAudio from '../../hooks/useAudio';


import Button from '../Button';

import './AudioButton.css';

const AudioButton = () => {
    const [isMuted, setIsMuted] = useLocalStorage<boolean>("SOUND_OFF", false);
    useAudio('/sounds/bee-hive-sound.mp3', true, isMuted);
    const onToggle = () => {
        setIsMuted(prevState => !prevState);
    }
    return (
        <Button className="audio-button" onClick={onToggle}>
            {isMuted ? <><BsFillVolumeUpFill />{" "}Sound On</> : <><BsFillVolumeMuteFill />{" "}Sound Off</>}
        </Button>
    )
}

export default AudioButton;