import React, { useState } from 'react'

import Button from '../../components/Button'
import Card from '../../components/Card'
import ConnectWallet from '../../components/ConnectWallet'
import InfoItem from '../../components/InfoItem'
import Input from '../../components/Input'
import Section from '../../components/Section'
import useActiveWeb3React from '../../hooks/useActiveWeb3React'
import { useBnbHive } from '../../providers/BNBHiveProvider/BNBHiveContext'
import { fromWeiWithDecimals, roundTo } from '../../utils'

import './HireSection.css'


interface ErrorMessageProps {
  isInitialised: boolean
  parsedAmount: number;
  minDeposit: number;
  maxDeposit: number;
  balance: number;
}

const ErrorMessage: React.FC<ErrorMessageProps> = ({isInitialised, parsedAmount, minDeposit, maxDeposit, balance }) => {
  const isLowerMinimumAmount = parsedAmount < minDeposit;
  const isHigherMaximumAmount = parsedAmount > maxDeposit;
  const isEnoughBalance = parsedAmount < balance;
  let text = "";
  if (!isInitialised) {
    text = "Mining is not initialized yet.";
  } else if(isLowerMinimumAmount) {
    text = `Minimum deposit amount is ${minDeposit} BNB`;
  } else if(isHigherMaximumAmount) {
    text = `Your maximum deposit amount is ${roundTo(maxDeposit, 2)} BNB for now.`;
  } else if(!isEnoughBalance) {
    text = `Not enough BNB`;
  }
  if (!text) return null;
  return (
    <div className="hire-form__error-message">
      {text}
    </div>
  )
}

const HireForm = () => {
  const { account, balance } = useActiveWeb3React()
  const bnbHive = useBnbHive()
  const dailyIncomePercent = bnbHive.stats.dailyReturnPercent
  const [amount, setAmount] = useState<string>('1')
  const [loading, setLoading] = useState<boolean>(false)
  const handleChange = (e: React.SyntheticEvent<HTMLInputElement>) => {
    const { value } = e.target as HTMLInputElement

    const formattedValue = value
      .replace(/[^0-9.]/g, '')
      .replace(/(\..*)\./g, '$1')

    setAmount(formattedValue)
  }

  const parsedAmount = parseFloat(amount)
  const refTotalPercent = bnbHive.stats.refLevels.reduce((acc, level) => acc + level.percent, 0);
  const restPercent = 100 - bnbHive.stats.devFee - bnbHive.stats.vaultFee - refTotalPercent;
  const restDeposit = parsedAmount * restPercent / 100; 
  const honeyAmount = roundTo(restDeposit * bnbHive.stats.honeyToBnbRatio, 3);
  const beesAmount = roundTo(honeyAmount / bnbHive.stats.honeyToBeeRatio, 0);
  const dailyIncomeAmount = roundTo(
    (parsedAmount * dailyIncomePercent) / 100,
    2,
  )

  const onHire = async () => {
    try {
      setLoading(true)
      if (bnbHive.contract && account) {
        await bnbHive.contract.deposit(amount, bnbHive.referrerAddress, account)
        setLoading(false)
      } else {
        throw new Error('No account')
      }
    } catch (e) {
      setLoading(false)
      alert((e as any).message)
    }
  }

  const isInitialised = bnbHive.stats.initialized

  const { maxDeposit, minDeposit } = bnbHive.account;

  const parsedBalance = parseFloat(fromWeiWithDecimals(balance));

  const canDeposit = (parsedAmount <= maxDeposit) && (parsedAmount >= minDeposit) && isInitialised && (parsedAmount <= parsedBalance);

  return (
    <div className="hire-form">
      <div className="hire-form__label">
        Enter amount of BNB you want to spend to hire bees:
      </div>
      <div className="hire-form__inputs">
        <Input value={amount} onChange={handleChange} affix="BNB" />
        {account ? (
          <Button
            type="primary"
            onClick={onHire}
            loading={loading}
            disabled={!canDeposit}
          >
            Hire Bees
          </Button>
        ) : (
          <ConnectWallet />
        )}
      </div>
      <ErrorMessage
        isInitialised={isInitialised}
        parsedAmount={parsedAmount}
        minDeposit={minDeposit}
        maxDeposit={maxDeposit}
        balance={parsedBalance}
      />
      {/* <div className="hire-form__predict-calcs">
        You will hire ~{beesAmount} BEES for {parsedAmount} BNB.
      </div> */}
      <div className="hire-form__predict-calcs">
        Your future income ~ {dailyIncomeAmount} BNB daily / ~ {roundTo(dailyIncomeAmount * 7, 3)}{' '}
        BNB weekly
      </div>
    </div>
  )
}

const HireSection = () => {
  const { balance } = useActiveWeb3React()
  const { account, stats } = useBnbHive()
  const dailyIncome = account.dailyIncome

  const refTotalPercent = stats.refLevels.reduce((acc, level) => acc + level.percent, 0);
  const restPercent = 100 - stats.devFee - stats.vaultFee - refTotalPercent;

  const beeToBnbRatio = 1 / stats.honeyToBnbRatio / (restPercent / 100) * stats.honeyToBeeRatio;
  const beeAmountToDisplayRatio = 100000;

  return (
    <Section className="hire-section">
      <Card>
        <InfoItem label="Your Balance">
          {roundTo(fromWeiWithDecimals(balance), 2)} BNB
        </InfoItem>
        <InfoItem label="Your Bees" loading={account.loading}>
          {roundTo(account.bees)}
        </InfoItem>
        <InfoItem label="BNB Per Week" loading={account.loading}>
          ~ {roundTo(dailyIncome * 7, 2)} BNB
        </InfoItem>
        <InfoItem label="Daily Return" loading={stats.loading}>
          {stats.dailyReturnPercent}%
        </InfoItem>
        <HireForm />
        {/* <InfoItem label="Honey price" loading={stats.loading}>
          {roundTo(1 / stats.honeyToBnbRatio, 6)} BNB
        </InfoItem>
        <InfoItem label={`${beeAmountToDisplayRatio} BEES price`} loading={stats.loading}>
          ~{roundTo(beeToBnbRatio * beeAmountToDisplayRatio, 3)} BNB
        </InfoItem> */}
        <InfoItem label="Total deposited" loading={account.loading}>
          {account.totalDeposit} BNB / {roundTo(account.totalDeposit + account.maxDeposit)} BNB
        </InfoItem>
        <InfoItem label="Minimum deposit" loading={account.loading}>
          {account.minDeposit} BNB
        </InfoItem>
      </Card>
    </Section>
  )
}

export default HireSection
