import React from "react";
import { FaDiscord, FaTelegram, FaTwitter, FaYoutube } from "react-icons/fa";
import { GiWhiteBook } from "react-icons/gi";

import BscScan from "../Icons/BscScan";
import TechRate from "../Icons/TechRate";

import "./SocialIcon.css";

const icons: any = {
  bscscan: BscScan,
  telegram: FaTelegram,
  twitter: FaTwitter,
  discord: FaDiscord,
  docs: GiWhiteBook,
  audit: TechRate,
  youtube: FaYoutube,
};

const SocialIcon: React.FC<{ type: string }> = ({ type }) => {
  const Icon = icons[type];
  return (
    <div className="social-icon">
      <Icon className={`social-icon--${type}`} size={20} />
    </div>
  );
};

export default SocialIcon;
