import React from "react";
import config from "../../config";
import ChainId from "../../constants/ChainId";
import SocialIcon from "./SocialIcon";

import "./Socials.css";

const contractAddress =
  config.depositContract[ChainId.BSC] ||
  config.depositContract[ChainId.BSC_TESTNET];

const socials = [
  {
    type: "bscscan",
    title: "Contract",
    href: `https://bscscan.com/address/${contractAddress}`,
  },
  {
    type: "youtube",
    title: "BNB Hive Youtube Channel",
    href: "https://www.youtube.com/channel/UCUrgJnu1xIq5igZQMGKAvuw",
  },
  {
    type: "telegram",
    title: "Telegram",
    href: "https://t.me/Bnb_hive_official",
  },
  {
    type: "twitter",
    title: "Twitter",
    href: "https://twitter.com/bnb_hive",
  },
  {
    type: "docs",
    title: "BNB Hive WhitePaper",
    href: "/BNBHiveWhitePaper.pdf",
  },
  {
    type: "audit",
    title: "TechRate Audit Report",
    href: "/BNBHive-TechRate-Audit.pdf",
  },
];

const Social: React.FC<any> = ({ type, title, href }) => (
  <div className="social">
    <a href={href} title={title} target="_blank" rel="noopener">
      <SocialIcon type={type} />
    </a>
  </div>
);

const Socials = () => (
  <div className="socials">
    {socials.map((item, index) => (
      <Social key={index} {...item} />
    ))}
  </div>
);

export default Socials;
