import { useState, useEffect } from "react";

const useAudio = (url: string, isInfinitePlay?: boolean, isMuted?: boolean) => {
  const [userInteracted, setUserInteracted] = useState<boolean>(false);
  const [audio] = useState(new Audio(url));
  const [playing, setPlaying] = useState<boolean>(false);

  const toggle = () => setPlaying(!playing);

  useEffect(() => {
    isMuted ? audio.pause() : audio.play();
  }, [isMuted]);

  useEffect(() => {
    playing ? audio.play() : audio.pause();
  }, [playing]);

  const onAudioEnd = () => {
    if (isInfinitePlay) {
      audio.play();
    } else {
      setPlaying(false);
    }
  };

  useEffect(() => {
    // document.addEventListener("click", startAudioIfPossible);
    audio.addEventListener("ended", onAudioEnd);
    return () => {
      // document.removeEventListener("click", startAudioIfPossible);
      audio.removeEventListener("ended", onAudioEnd);
    };
  }, []);

  return { playing, toggle };
};

export default useAudio;
